:root {
  --base: $primary;
}

html,
body {

  font-family: Bitwise;

  color: rgb(50, 50, 50);
  -ms-user-select: None;
  -moz-user-select: None;
  -webkit-user-select: None;
  user-select: None;
  height: 100%;

}

.Desktop {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: 50px 50px;

  background-image: linear-gradient(0deg, transparent 24%, rgba(255, 255, 255, 0.311) 25%, rgba(255, 255, 255, 0.311) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, 0.311) 75%, rgba(255, 255, 255, 0.311) 76%, transparent 77%, transparent), linear-gradient(90deg, transparent 24%, rgba(255, 255, 255, 0.311) 25%, rgba(255, 255, 255, 0.311) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, 0.311) 75%, rgba(255, 255, 255, 0.311) 76%, transparent 77%, transparent);
  overflow: hidden;
}


@font-face {
  font-family: Bitwise;
  src: url(./fonts/AndaleMono.ttf);
}

.folder img {
  max-width: 100px;
}



.folder {
  width: 110px;
  padding: 10px;
  align-content: center;
  text-align: center;
}

.folder:hover {
  background: pink;
}

.folderWrapper img {
  width: 75px;
}

.folderWrapper p {
  font-family: Bitwise;
  font-size: 14pt;
  text-transform: uppercase;

}

.folderWrapper {


  text-align: center;
  padding-top: 20%;
  height: 100px;
  padding-right: 200px;

}

.folderWrapper div {
  padding-top: 20%;

}

.folderWrapperWrapper {
  display: grid;
  grid-template-columns: 100px 100px 1fr;
  grid-template-rows: auto auto auto auto auto;
  grid-auto-flow: column;
  grid-gap: 80px;
}

.sketchWrap {
  display: flex;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

.contentWrap {
  padding: 10%;

}

.contentWrap .holder {
  display: flex;
  flex-wrap: wrap;
  height: 80%;
}

.kunstOrdner img {
  width: 200px;
  margin: 20px;
}

.inputStyle {
  position: "absolute";
  right: 10;
  top: 100;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.topBar {
  position: absolute;
  display: grid;
  grid-template-columns: 100px 100px auto 20px 200px;
  grid-template-rows: 100%;
  top: 0;
  right: 0;
  left: 0;
  height: 30px;
  background-color: rgba(255, 255, 255, 0.612);
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  border-bottom: solid 1px rgba(0, 0, 0, 0.688);
}

.colorSelector {
  background-color: red;
  float: left;
  width: 3.2vw;
  height: 3.2vw;
  border: solid 0.5px rgba(0, 0, 0, 0.434);
  margin: 1.5vw;
  white-space: nowrap;
}

.topBar img {
  height: 25px;
}


#settingsPopup {
  display: none;
  background-color: grey;
  white-space: nowrap;
  position: absolute;
  left: 42%;
  right: 42%;
  top: 20%;
  bottom: 20%;
  margin: auto;
  padding-left: 25px;
  text-align: center;

  background: rgb(238, 237, 237);
  border: solid 0.5px grey;
  border-radius: 3px;
  -webkit-box-shadow: 1.2rem 1.2rem 0 rgba(0, 0, 0, 0.133);
  -moz-box-shadow: 1.2rem 1.2rem 0 rgba(0, 0, 0, 0.133);
  box-shadow: 1.2rem 1.2rem 0 rgba(0, 0, 0, 0.133);
  -webkit-background-clip: padding-box;
  -moz-background-clip: padding-box;
  background-clip: padding-box;
  overflow: hidden;
}

#settingsPopup #modalTopBar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 30px;
  background-color: rgb(216, 214, 214);
  border-bottom: solid rgb(34, 34, 34)y;
  text-align: center;
}

.topBar .time {
  position: absolute;
  right: 10px;
}

.topBar p {
  position: absolute;
  left: 10px;
}

::-webkit-scrollbar {
  width: 20px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: 0 0 5px grey;
  border-radius: 0px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(174, 174, 174);
  border-radius: 0px;
  border: solid 0.5px black;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #d0d0d0;
}

.settingsBtn {

  width: 20px;
  height: 20px;
  margin-top: 2px;
}